import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "bootstrap/dist/css/bootstrap.min.css";
import "../../css/Login.css";
//import {useNavigate} from "react-router-dom";
import {createBrowserHistory} from "history";

function AddCoordinatorLogin() {
    const [campuses, setCampuses] = useState([]);
    const [name, setName] = useState('');
    const [selectedCampus, setSelectedCampus] = useState('');
    const [coordID, setCoordID] = useState('');
    const [password, setPassword] = useState('');
    //const { navigate } = useNavigate();

    useEffect(() => {
        // Fetch campuses when the component mounts
        const fetchCampuses = async () => {
            try {
                const response = await axios.get('https://lmsacademicserver.netlify.app/api/campus/getAllCampuses');
                setCampuses(response.data);
            } catch (error) {
                console.error("Error fetching campuses:", error);
            }
        };

        fetchCampuses();
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();

        const history = createBrowserHistory();

        try {
            // Ensure selectedCampus is an array, even if it contains one item
            const campusArray = selectedCampus ? [selectedCampus] : [];

            const response = await axios.post('https://lmsacademicserver.netlify.app/api/coordinator/create', {
                name,
                assignCampus: campusArray,
                coordID,
                password
            });

            if (response.status === 201) {
                alert("Coordinator created successfully");
                history.push("/login");
                history.go(0);
            }
        } catch (error) {
            console.error("Error creating coordinator:", error);
            alert("Failed to create coordinator. Please try again.");
        }
    };


    return (
        <>
            <div className="maindiv">
                {/* Animated Area */}
                <div className="area">
                    <ul className="circles">
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>

                <div className="container m-5" id="container">
                    <div className="form-container half-width-md sign-in">
                        <div className="form">
                            <h3 className="fw-semibold p-3 loginCardColor rounded-4 text-white d-md-none text-center">
                                ACADEMIC DIVISION
                            </h3>
                            <h4 className="mt-3">Sign Up</h4>
                            <span className="mb-3">Add your username, select your campus, and set a password</span>

                            <form onSubmit={handleSubmit}>
                                <input
                                    placeholder="User Name"
                                    type="text"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                    required
                                />
                                <div className="input-group mb-3">
                                    <span className="input-group-text fw-bold">Campus&nbsp;:</span>
                                    <select
                                        className="form-select"
                                        aria-label="Campus"
                                        value={selectedCampus}
                                        onChange={(e) => setSelectedCampus(e.target.value)}
                                        required
                                    >
                                        <option value="">Select a Campus</option>
                                        {campuses.map(campus => (
                                            <option key={campus._id} value={campus._id}>
                                                {campus.campusName}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <input
                                    type="text"
                                    placeholder="CoordID"
                                    value={coordID}
                                    onChange={(e) => setCoordID(e.target.value)}
                                    required
                                />
                                <input
                                    type="password"
                                    placeholder="Password"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                    required
                                />
                                <button type="submit">Create Coordinator</button>
                            </form>
                        </div>
                    </div>
                    <div className="toggle-container d-none d-md-block">
                        <div className="toggle">
                            <div className="toggle-panel toggle-left">
                                <h1>Welcome Back!</h1>
                                <p>Enter your personal details to use all of the site's features</p>
                            </div>
                            <div className="toggle-panel toggle-right">
                                <h2>ACADEMIC DIVISION </h2>
                                <h5 className="mb-4">Coordinator Sign Up</h5>
                                <p>
                                    Register with your personal username, select your campus, and set a password to access
                                    classes and other features
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default AddCoordinatorLogin;
