import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

const ManageDirectorCoordinate = () => {
    const [coordinators, setCoordinators] = useState([]);
    const [editId, setEditId] = useState(null);
    const [editedCoordinator, setEditedCoordinator] = useState({});

    useEffect(() => {
        const fetchCoordinators = async () => {
            try {
                const response = await axios.get('https://lmsacademicserver.netlify.app/api/coordinator/getAllCoordinators');
                setCoordinators(response.data);
            } catch (error) {
                console.error("Error fetching coordinators:", error);
            }
        };

        fetchCoordinators();
    }, []);

    const handleEditClick = (coordinator) => {
        setEditId(coordinator._id);
        setEditedCoordinator(coordinator);
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditedCoordinator(prev => ({ ...prev, [name]: value }));
    };

    const handleSaveClick = async () => {

        
        try {
            await axios.put(`https://lmsacademicserver.netlify.app/api/coordinator/updateCoordinator/${editId}`, editedCoordinator);
            setEditId(null);
            // Refetch data or update state to reflect changes
            const response = await axios.get('https://lmsacademicserver.netlify.app/api/coordinator/getAllCoordinators');
            setCoordinators(response.data);
        } catch (error) {
            console.error("Error updating coordinator:", error);
        }
    };

    return (
        <div className="col-12 bg-light">
            <div className="row">
                <h2 className="fw-bold mt-5">Manage Coordinator</h2>
                <div className="col-12 col-lg-4 offset-0 offset-lg-8 d-flex gap-2 mt-4">
                    <input
                        className="form-control"
                        list="datalistOptions"
                        id="exampleDataList"
                        placeholder="Type to search..."
                    />
                    <datalist id="datalistOptions">
                        {/* Populate datalist options as needed */}
                    </datalist>
                    <Link to="add-payment" type="button" className="btn btn-primary">
                        New
                    </Link>
                </div>

                <div className="col-12 overflow-x-auto">
                    <div className="m-1 table-responsive">
                        <div className="p-2">
                            <p className="fw-bold fs-5 text-black-50">All Coordinators</p>
                        </div>

                        <table className="table">
                            <thead>
                            <tr>
                                <th scope="col">Name</th>
                                <th scope="col">Assign Campus</th>
                                <th scope="col">Coordinator Is Director</th>
                                <th scope="col">CoordID</th>
                                <th scope="col">Password</th>
                                <th scope="col">Actions</th>
                            </tr>
                            </thead>
                            <tbody>
                            {coordinators.map(coordinator => (
                                <tr key={coordinator._id}>
                                    <td>
                                        {editId === coordinator._id ? (
                                            <input
                                                type="text"
                                                name="name"
                                                value={editedCoordinator.name || ''}
                                                onChange={handleInputChange}
                                            />
                                        ) : (
                                            coordinator.name
                                        )}
                                    </td>
                                    <td>
                                        {coordinator.assignCampus.map(campus => campus.campusName).join(', ')}
                                    </td>
                                    <td>
                                        {editId === coordinator._id ? (
                                            <select
                                                name="coordinatorIsDirector"
                                                value={editedCoordinator.coordinatorIsDirector || ''}
                                                onChange={handleInputChange}
                                            >
                                                <option value="true">True</option>
                                                <option value="false">False</option>
                                            </select>
                                        ) : (
                                            coordinator.coordinatorIsDirector.toString()
                                        )}
                                    </td>
                                    <td>{coordinator.coordID}</td>
                                    <td>{coordinator.password}</td>
                                    <td>
                                        {editId === coordinator._id ? (
                                            <>
                                                <button onClick={handleSaveClick} className="btn btn-success btn-sm">Save</button>
                                                <button onClick={() => setEditId(null)} className="btn btn-secondary btn-sm">Cancel</button>
                                            </>
                                        ) : (
                                            <button onClick={() => handleEditClick(coordinator)} className="btn btn-dark btn-sm">Edit</button>
                                        )}
                                    </td>
                                </tr>
                            ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ManageDirectorCoordinate;
