import React, { useEffect, useState } from "react";
import axios from "axios";
import {Link} from "react-router-dom";

const ManageCampus = () => {
  const [students, setStudents] = useState([]);
  const [editStudentId, setEditStudentId] = useState(null);
  const [editStudentData, setEditStudentData] = useState({
    campusName: "",
    courseTitle: [],
    paymentPlan: "",
    studentID: "",
    studentName: "",
    StudentNIC: "",
    childHoodNIC: "",
    password: "",
    whatsAppMobileNo1: "",
    disCount: "",
    paymentAmount: "",
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchStudents = async () => {
      setLoading(true); // Set loading state
      try {
        const response = await axios.get(
            "https://lmsacademicserver.netlify.app/api/coordinatorAddStudent/getAllStudents"
        );
        setStudents(response.data);
      } catch (error) {
        console.error("Error fetching students:", error);
        alert("Failed to fetch students. Please try again.");
      } finally {
        setLoading(false); // Reset loading state
      }
    };

    fetchStudents();
  }, []);

  const handleEditClick = (student) => {
    setEditStudentId(student._id);
    setEditStudentData({
      campusName: student.campusName,
      courseTitle: student.courseTitle,
      paymentPlan: student.paymentPlan,
      studentID: student.studentID,
      studentName: student.studentName,
      StudentNIC: student.StudentNIC,
      childHoodNIC: student.childHoodNIC,
      password: "", // Do not pre-fill the password
      whatsAppMobileNo1: student.whatsAppMobileNo1,
      disCount: student.disCount,
      paymentAmount: student.paymentAmount,
    });
  };

  const handleSaveClick = async () => {
    try {
      console.log("Student ID being updated:", editStudentId);
      console.log("Data being sent:", editStudentData);

      // Make a PUT request to update the student data
      const response = await axios.put(
          `https://lmsacademicserver.netlify.app/api/coordinatorAddStudent/updateStudent/${editStudentId}`,
          editStudentData
      );

      console.log("Response from update:", response);

      if (response.status === 200) {
        setStudents((prevStudents) =>
            prevStudents.map((student) =>
                student._id === editStudentId ? { ...student, ...editStudentData } : student
            )
        );
        resetEditState();
      } else {
        throw new Error("Update failed with status: " + response.status);
      }
    } catch (error) {
      console.error("Error updating student:", error);
      alert("Failed to update student. Please try again.");
    }
  };


  const resetEditState = () => {
    setEditStudentId(null); // Exit edit mode
    setEditStudentData({
      campusName: "",
      courseTitle: [],
      paymentPlan: "",
      studentID: "",
      studentName: "",
      StudentNIC: "",
      childHoodNIC: "",
      password: "",
      whatsAppMobileNo1: "",
      disCount: "",
      paymentAmount: "",
    });
  };

  const handleCancelClick = () => {
    resetEditState();
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEditStudentData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  if (loading) {
    return <div>Loading students...</div>; // Loading state message
  }

  return (
      <div className="col-12 bg-light">
        <div className="row">
          <h2 className="fw-bold mt-5">Manage Students</h2>
          <div className="col-12 col-lg-4 offset-0 offset-lg-8 d-flex gap-2 mt-4">
            <input
                className="form-control"
                list="datalistOptions"
                id="exampleDataList"
                placeholder="Type to search..."
            />
            <datalist id="datalistOptions">
              {students.map((student) => (
                  <option key={student._id} value={student.studentName} />
              ))}
            </datalist>
            <Link to="add-student" type="button" className="btn btn-primary">
              New
            </Link>
          </div>

          <div className="col-12 overflow-x-auto">
            <div className="m-1 table-responsive">
              <div className="p-2">
                <p className="fw-bold fs-5 text-black-50">All Students</p>
              </div>

              <table className="table">
                <thead>
                <tr>
                  <th scope="col">Campus Name</th>
                  <th scope="col">Course Name</th>
                  <th scope="col">Payment Plan</th>
                  <th scope="col">Student ID</th>
                  <th scope="col">Student Name</th>
                  <th scope="col">Student NIC</th>
                  <th scope="col">Childhood NIC</th>
                  <th scope="col">Password</th>
                  <th scope="col">Mobile No</th>
                  <th scope="col">Action</th>
                </tr>
                </thead>
                <tbody>
                {students.length > 0 ? (
                    students.map((student) => (
                        <tr key={student._id}>
                          <td>{student.campusName}</td>
                          <td>
                            {student.courseTitle.length > 0
                                ? student.courseTitle.map((course, index) => (
                                    <span key={index}>
                                {course.courseTitle}
                                      {index < student.courseTitle.length - 1 && ", "}
                              </span>
                                ))
                                : "Unknown"}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="paymentPlan"
                                    value={editStudentData.paymentPlan}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.paymentPlan || "N/A"
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="studentID"
                                    value={editStudentData.studentID}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.studentID
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="studentName"
                                    value={editStudentData.studentName}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.studentName
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="StudentNIC"
                                    value={editStudentData.StudentNIC}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.StudentNIC
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="childHoodNIC"
                                    value={editStudentData.childHoodNIC}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.childHoodNIC
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="password"
                                    name="password"
                                    value={editStudentData.password}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                "********" // Hide password display for security
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <input
                                    type="text"
                                    name="whatsAppMobileNo1"
                                    value={editStudentData.whatsAppMobileNo1}
                                    onChange={handleInputChange}
                                />
                            ) : (
                                student.whatsAppMobileNo1
                            )}
                          </td>
                          <td>
                            {editStudentId === student._id ? (
                                <>
                                  <button
                                      type="button"
                                      className="btn btn-dark py-0"
                                      onClick={handleSaveClick}
                                  >
                                    Save
                                  </button>
                                  <button
                                      type="button"
                                      className="btn btn-light py-0 mx-1"
                                      onClick={handleCancelClick}
                                  >
                                    Cancel
                                  </button>
                                </>
                            ) : (
                                <button
                                    type="button"
                                    className="btn btn-warning py-0"
                                    onClick={() => handleEditClick(student)}
                                >
                                  Edit
                                </button>
                            )}
                          </td>
                        </tr>
                    ))
                ) : (
                    <tr>
                      <td colSpan="10" className="text-center">
                        No students found.
                      </td>
                    </tr>
                )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
  );
};

export default ManageCampus;
