import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const ManageSubject = () => {
  const [subjects, setSubjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [editingSubjectId, setEditingSubjectId] = useState(null);
  const [courses, setCourses] = useState([]); // To store courses
  const [selectedCourseId, setSelectedCourseId] = useState("");
  const [editedSubject, setEditedSubject] = useState({
    subjectId: "",
    subjectName: "",
    classDay: "",
    sessionStartTime: "",
    sessionEndTime: "",
  });

  // Function to fetch all subjects
  const fetchSubjects = async () => {
    try {
      const response = await axios.get('https://lmsacademicserver.netlify.app/api/subject/getAllSubjects');
      console.log('Subjects response:', response.data); // Log the response to inspect its structure

      if (Array.isArray(response.data)) {
        setSubjects(response.data); // Direct array response
      } else if (response.data && Array.isArray(response.data.subjects)) {
        setSubjects(response.data.subjects); // Handle nested subjects array
      } else {
        throw new Error('Unexpected response format');
      }
    } catch (err) {
      console.error('Error fetching subjects:', err.message);
      setError('Error fetching subjects');
    } finally {
      setLoading(false);
    }
  };



  // Function to fetch all courses
  const fetchCourses = async () => {
    try {
      const response = await axios.get('https://lmsacademicserver.netlify.app/api/course/getAllCourses');
      if (Array.isArray(response.data)) {
        setCourses(response.data);
      } else if (response.data && response.data.courses && Array.isArray(response.data.courses)) {
        setCourses(response.data.courses); // Handle nested courses
      } else {
        console.error('Unexpected response format for courses:', response.data);
        setError('Unexpected response format for courses');
      }
    } catch (err) {
      console.error('Error fetching courses:', err);
      setError('Error fetching courses');
    }
  };

  useEffect(() => {
    fetchSubjects();
    fetchCourses();
  }, []);


  const handleEditClick = (subject) => {
    setEditingSubjectId(subject._id);
    setEditedSubject({
      subjectId: subject.subjectId,
      subjectName: subject.subjectName,
      classDay: subject.classDay,
      sessionStartTime: subject.sessionStartTime,
      sessionEndTime: subject.sessionEndTime,
    });
  };

  const handleSaveClick = async () => {
    const token = localStorage.getItem('token'); // Retrieve token from localStorage

    try {
      const response = await axios.put(`https://lmsacademicserver.netlify.app/api/subject/updateSubject/${editingSubjectId}`, editedSubject, {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      // Update the state with the new subject information
      setSubjects((prevSubjects) =>
          prevSubjects.map((subject) =>
              subject._id === editingSubjectId ? response.data : subject
          )
      );

      // Clear the editing state
      setEditingSubjectId(null);
      fetchCourses();
    } catch (error) {
      console.error('Error updating subject:', error);
      setError('Error updating subject');
    }
  };

  const handleCancelClick = () => {
    setEditingSubjectId(null);
    setEditedSubject({
      subjectId: "",
      subjectName: "",
      classDay: "",
      sessionStartTime: "",
      sessionEndTime: "",
    });
  };

  const handleCourseSelect = (e) => {
    setSelectedCourseId(e.target.value); // Set selected course ID
  };

  const formatTime = (timeStr) => {
    if (!timeStr) return '';
    const time = new Date(`1970-01-01T${timeStr}Z`);
    return time.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', hour12: true });
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>{error}</p>;

  return (
      <>
        <div className="col-12 bg-light">
          <div className="row">
            <h2 className="fw-bold mt-5">Manage Subjects</h2>

            {/* Course Select Dropdown */}
            <div className="col-12 col-lg-4 offset-0 offset-lg-8 d-flex gap-2 mt-4">
              <select
                  className="form-select"
                  value={selectedCourseId}
                  onChange={handleCourseSelect}
              >
                <option value="" disabled>
                  Select Course
                </option>
                {courses.map((course) => (
                    <option key={course._id} value={course._id}>
                      {course.courseTitle}
                    </option>
                ))}
              </select>
            </div>

            {/* Search Input */}
            <div className="col-12 col-lg-4 offset-0 offset-lg-8 d-flex gap-2 mt-4">
              <input
                  className="form-control"
                  list="datalistOptions"
                  id="exampleDataList"
                  placeholder="Type to search..."
              />
              <datalist id="datalistOptions">
                {/* Add relevant options here */}
              </datalist>
              <Link to="add-subject" type="button" className="btn btn-primary">
                New
              </Link>
            </div>

            {/* Subjects Table */}
            <div className="mt-5 rounded-3 col-12">
              <div className="row gap-5 d-flex justify-content-center">
                <div className="col-12 overflow-x-auto">
                  <div className="m-1">
                    <div className="p-2">
                      <p className="fw-bold fs-5 text-black-50">All Subjects</p>
                    </div>

                    <table className="table">
                      <thead>
                      <tr>
                        <th scope="col">Subject ID</th>
                        <th scope="col">Subject Name</th>
                        <th scope="col">Class Day</th>
                        <th scope="col">Session Start Time</th>
                        <th scope="col">Session End Time</th>
                        <th scope="col">Created Date</th>
                        <th scope="col">Updated Date</th>
                        <th scope="col">Action</th>
                      </tr>
                      </thead>
                      <tbody>
                      {subjects.length > 0 ? (
                          subjects.map((subject) => (
                              <tr key={subject._id}>
                                <td>{subject.subjectId}</td>
                                <td>
                                  {editingSubjectId === subject._id ? (
                                      <input
                                          type="text"
                                          value={editedSubject.subjectName}
                                          onChange={(e) =>
                                              setEditedSubject({...editedSubject, subjectName: e.target.value})
                                          }
                                      />
                                  ) : (
                                      subject.subjectName
                                  )}
                                </td>
                                <td>
                                  {editingSubjectId === subject._id ? (
                                      <select
                                          className="form-select"
                                          value={editedSubject.classDay}
                                          onChange={(e) =>
                                              setEditedSubject({...editedSubject, classDay: e.target.value})
                                          }
                                      >
                                        <option value="">Select Day</option>
                                        <option value="Monday">Monday</option>
                                        <option value="Tuesday">Tuesday</option>
                                        <option value="Wednesday">Wednesday</option>
                                        <option value="Thursday">Thursday</option>
                                        <option value="Friday">Friday</option>
                                        <option value="Saturday">Saturday</option>
                                        <option value="Sunday">Sunday</option>
                                      </select>
                                  ) : (
                                      subject.classDay
                                  )}
                                </td>

                                <td>
                                  {editingSubjectId === subject._id ? (
                                      <input
                                          type="time"
                                          value={editedSubject.sessionStartTime}
                                          onChange={(e) =>
                                              setEditedSubject({...editedSubject, sessionStartTime: e.target.value})
                                          }
                                      />
                                  ) : (
                                      formatTime(subject.sessionStartTime)
                                  )}
                                </td>
                                <td>
                                  {editingSubjectId === subject._id ? (
                                      <input
                                          type="time"
                                          value={editedSubject.sessionEndTime}
                                          onChange={(e) =>
                                              setEditedSubject({...editedSubject, sessionEndTime: e.target.value})
                                          }
                                      />
                                  ) : (
                                      formatTime(subject.sessionEndTime)
                                  )}
                                </td>
                                <td>{new Date(subject.createdAt).toLocaleDateString()}</td>
                                <td>{new Date(subject.updatedAt).toLocaleDateString()}</td>
                                <td>
                                  {editingSubjectId === subject._id ? (
                                      <>
                                        <button
                                            type="button"
                                            className="btn btn-dark py-0"
                                            onClick={handleSaveClick}
                                        >
                                          Save
                                        </button>
                                        <button
                                            type="button"
                                            className="btn btn-light py-0 ml-2"
                                            onClick={handleCancelClick}
                                        >
                                          Cancel
                                        </button>
                                      </>
                                  ) : (
                                      <button
                                          type="button"
                                          className="btn btn-dark py-0"
                                          onClick={() => handleEditClick(subject)}
                                      >
                                        Update
                                      </button>
                                  )}
                                </td>
                              </tr>
                          ))
                      ) : (
                          <tr>
                            <td colSpan="8">No subjects found</td>
                          </tr>
                      )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
  );
};

export default ManageSubject;
