import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AddSCampus = () => {
    const [campusName, setCampusName] = useState("");
    const [manageFiled, setManageFiled] = useState("");
    const [error, setError] = useState("");
    const navigate = useNavigate();
    const token = localStorage.getItem("token");



    /*useEffect(() => {
        const fetchCourses = async () => {
            try {
                const response = await axios.get("https://lmsacademicserver.netlify.app/api/course/getAllCourses", {
                    headers: { Authorization: `Bearer ${token}` },
                });
                const fetchedCourses = response.data.courses;
                if (Array.isArray(fetchedCourses)) {
                    setCourses(fetchedCourses);
                } else {
                    console.error("Courses data is not an array:", response.data);
                    setError("Failed to load courses.");
                }
            } catch (err) {
                console.error("Error fetching courses:", err);
                setError("Failed to load courses.");
            }
        };
        fetchCourses();
    }, [token]);*/

    const handleAddCampus = async () => {
        if (!campusName || !manageFiled) {
            setError("Please fill in all the fields.");
            return;
        }

        const newCampus = {
            campusName,
            manageFiled
        }

        try {
            const response = await axios.post(
                "https://lmsacademicserver.netlify.app/api/campus/create",
                newCampus,
                {
                    headers: {
                        Authorization: `Bearer ${token}`,
                        "Content-Type": "application/json",
                    },
                }
            );
            if (response.status === 201) {
                alert("Campus added successfully");
                setCampusName(""); // Clear form fields
                navigate("/academic/manage-campus");
            } else {
                setError("Failed to add Campus. Please check the details.");
            }
        }catch (error){
            setError("Failed to add Campus. Please try again.");
            console.error("Error adding Campus:", error);
        }


        /*try {
            const response = await axios.post(
                "https://lmsacademicserver.netlify.app/api/campus/create",
                {
                    campusName,
                    course: selectedCourses, // Ensure the field name matches the schema
                },
                {
                    headers: {
                        Authorization: `Bearer ${token}`,
                        "Content-Type": "application/json",
                    },
                }
            );

            if (response.status === 201) {
                alert("Campus added successfully");
                setCampusName(""); // Clear form fields

                navigate("/academic/manage-campus");
            } else {
                setError("Failed to add Campus. Please check the details.");
            }
        } catch (error) {
            setError(
                error.response?.data?.message || "Failed to add Campus. Please try again."
            );
            console.error("Error adding Campus:", error);
        }*/
    };

    return (
        <>
            <div className="form-bg-image">
                <div className="col-12 d-flex justify-content-center mt-4">
                    <div
                        className="row col-lg-6 shadow-lg m-3 rounded-3 h-auto mt-4 p-3 formColour border border-3 border-end-0 border-start-0 border-primary">
                        <h4 className="fw-bold text-black-50 mt-3 mb-4">Add New Campus</h4>
                        {error && <div className="alert alert-danger">{error}</div>}
                        <div className="col-12">
                            <div className="input-group mb-3">
                                <span className="input-group-text fw-bold" id="basic-addon3">
                                    Campus Name&nbsp;&nbsp;:
                                </span>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="basic-url"
                                    aria-describedby="basic-addon3"
                                    value={campusName}
                                    onChange={(e) => setCampusName(e.target.value)}
                                />
                            </div>
                        </div>

                        <div className="col-12">
                            <div className="input-group mb-3">
                                <span className="input-group-text fw-bold" id="basic-addon3">
                                    Major Studied Field&nbsp;&nbsp;:
                                </span>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="basic-url"
                                    aria-describedby="basic-addon3"
                                    value={manageFiled}
                                    onChange={(e) => setManageFiled(e.target.value)}
                                />
                            </div>
                        </div>


                        {/* Add and Cancel Buttons */}
                        <div className="col-12 text-end">
                            <button type="button" className="btn btn-primary m-2" onClick={handleAddCampus}
                             style={{backgroundColor:"rgb(13, 13, 175)"}}
                            >
                                Save
                            </button>
                            <button type="button" className="btn btn-dark py-0 m-2"
                                    onClick={() => navigate("/academic/manage-campus")}>
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default AddSCampus;
