import { Link } from "react-router-dom";
import React from "react";

const EducationStudent = () => {
    return (
        <div className="container-fluid min-vh-100 bg-light d-flex align-items-center justify-content-center">
            <div className="row w-100">
                <div className="col-12 text-center">
                    <h2 className="fw-bold mt-5 mb-4">Manage Your Details</h2>
                </div>

                {/* Card for Education */}
                <div className="col-md-4 col-sm-12 mb-4">
                    <div className="card shadow-lg text-center p-4">
                        <h3 className="card-title mb-3">Personal Details</h3>
                        <p className="card-text text-muted">
                            Add or Update Your Personal Details.
                        </p>
                        <Link to="/profile/manage-education" className="btn btn-primary" style={{ fontSize: "20px" }}>
                            Manage Personal Details
                        </Link>
                    </div>
                </div>

                {/* Card for WorkPlace */}
                <div className="col-md-4 col-sm-12 mb-4">
                    <div className="card shadow-lg text-center p-4">
                        <h3 className="card-title mb-3">WorkPlace</h3>
                        <p className="card-text text-muted">
                            Add or update your workplace details.
                        </p>
                        <Link to="/profile/manage-worksSpace" className="btn btn-primary" style={{ fontSize: "20px" }}>
                            Manage WorkPlace
                        </Link>
                    </div>
                </div>

                {/* Card for Student Portal */}
                <div className="col-md-4 col-sm-12 mb-4">
                    <div className="card shadow-lg text-center p-4">
                        <h3 className="card-title mb-3">Student Portal</h3>
                        <p className="card-text text-muted">
                            Go back to your student portal for more details.
                        </p>
                        <Link to="/student" className="btn btn-primary" style={{ fontSize: "20px" }}>
                            Go to Student Portal
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EducationStudent;
