import React, { useEffect, useState } from 'react';
import axios from 'axios';

const PaymentDetailCard = () => {
  const [student, setStudent] = useState(null);
  const [loading, setLoading] = useState(true);

  // Helper function to format currency
  const formatCurrency = (amount) => {
    if (!amount) return 'N/A';
    return parseFloat(amount).toLocaleString('en-US', { minimumFractionDigits: 2 });
  };

  // Fetch payment details for a specific student
  useEffect(() => {
    const fetchStudentDetails = async () => {
      try {
        // Get studentId from localStorage
        const storedStudentId = localStorage.getItem('studentID');
        if (!storedStudentId) {
          console.error('No studentID found in localStorage');
          setLoading(false);
          return;
        }

        // Fetch all students from the API
        const response = await axios.get('https://lmsacademicserver.netlify.app/api/coordinatorAddStudent/getAllStudents');
        const studentData = response.data.find((student) => student.studentID === decodeURIComponent(storedStudentId));

        if (studentData) {
          setStudent(studentData);
        } else {
          console.error('Student not found');
        }
      } catch (error) {
        console.error('Error fetching student data:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchStudentDetails();
  }, []);

  // If the data is still being loaded
  if (loading) {
    return <div>Loading...</div>;
  }

  // If no student is found
  if (!student) {
    return <div>No student found</div>;
  }

  const course = student?.courseTitle?.[0];
  const paymentPlan = student?.paymentPlan;

  // Parse the paymentPlan string into an array of installments
  const installments = paymentPlan
      ? paymentPlan.split('\n').reduce((acc, line) => {
        if (line.includes('Installment Number')) {
          const installmentNumber = line.split(': ')[1]?.trim();
          acc.push({ installmentNumber });
        } else if (line.includes('Amount')) {
          acc[acc.length - 1].amount = line.split(': ')[1]?.trim();
        } else if (line.includes('Due Date')) {
          acc[acc.length - 1].dueDate = line.split(': ')[1]?.trim();
        } else if (line.includes('Status')) {
          acc[acc.length - 1].status = line.split(': ')[1]?.trim();
        }
        return acc;
      }, [])
      : [];

  return (
      <div className="col-12 col-lg-6 p-3 mb-2">
        <div className="shadow p-3 rounded CourseAndPaymentCardHight">
          <h3 className="fw-bold">Payment Details</h3>
          <div className="col-12 mt-4 fw-semibold">
            <p className="fw-semibold text-primary">
              Course Fee: <span className="text-black">{formatCurrency(course?.totalCourseFee)}</span>
            </p>
            <p className="fw-semibold text-primary">
              Discount: <span className="text-black">{formatCurrency(student?.disCount)}</span>
            </p>
            <hr className="border border-2 w-75 border-black" />
            <p className="fw-semibold text-primary">
              Gross Fee: <span className="text-black">{formatCurrency(student?.paymentAmount)}</span>
            </p>
            <hr className="border border-2 w-75 border-black pb-0 mb-0" />
            <hr className="border border-2 w-75 border-black pt-0 mt-1" />
            <div className="col-12 mt-4 shadow">
              <div className="table-responsive CourseAndPaymentCardTableHight">
                <table className="table">
                  <thead>
                  <tr>
                    {/* <th scope="col">#</th> */}
                    <th scope="col">Installment</th>
                    <th scope="col" className="text-end">Amount (Rs)</th>
                    <th scope="col" className="text-end">Due Date</th>
                    <th scope="col" className="text-end">Status</th>
                  </tr>
                  </thead>
                  <tbody>
                  {installments.length > 0 ? (
                      installments.map((installment, index) => (
                          <tr key={index}>
                            {/* <td>{index + 1}</td> */}
                            <td>{installment.installmentNumber || 'N/A'}</td>
                            <td className="text-end">{formatCurrency(installment.amount)}</td>
                            <td className="text-end">{installment.dueDate || 'N/A'}</td>
                            <td className="text-end">{installment.status || 'N/A'}</td>
                          </tr>
                      ))
                  ) : (
                      <tr>
                        <td colSpan="5" className="text-center">No Installment Data Available</td>
                      </tr>
                  )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
  );
};

export default PaymentDetailCard;
