import React, { useEffect, useState } from 'react';
import axios from 'axios';

const CourseDetailCard = () => {
  const [student, setStudent] = useState(null);
  const [subjects, setSubjects] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStudentDetails = async () => {
      try {
        const storedStudentId = localStorage.getItem('studentID');
        if (!storedStudentId) {
          console.error('No studentID found in localStorage');
          setLoading(false);
          return;
        }

        const response = await axios.get('https://lmsacademicserver.netlify.app/api/coordinatorAddStudent/getAllStudents');
        const studentData = response.data.find((student) => student.studentID === decodeURIComponent(storedStudentId));

        if (studentData) {
          setStudent(studentData);

          // Fetch subjects by subject IDs if available
          const subjectIds = studentData.courseTitle.flatMap(course => course.subject);
          if (subjectIds.length > 0) {
            try {
              const subjectResponse = await axios.get(`https://lmsacademicserver.netlify.app/api/subject/getAllSubjects?ids=${subjectIds.join(',')}`);
              setSubjects(subjectResponse.data.subjects || []);
            } catch (subjectError) {
              console.error('Error fetching subjects:', subjectError.response ? subjectError.response.data : subjectError.message);
            }
          } else {
            console.warn('No subjects found for this student');
          }
        } else {
          console.error('Student not found');
        }
      } catch (error) {
        console.error('Error fetching student data:', error.response ? error.response.data : error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchStudentDetails();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="col-12 col-lg-6 p-3 mb-2">
      <div className="shadow p-3 rounded CourseAndPaymentCardHight">
        <div className="row">
          <div className="col-6">
            <h3 className="fw-bold">Course Details</h3>
          </div>
          {/* <div className="col-6 text-end">
              <button className="btn btn-primary">Join Class</button>
            </div> */}
        </div>

        <div className="col-12 mt-4 fw-semibold">
          {student && student.courseTitle.length > 0 && (
            <>
              <p className="fw-semibold text-primary">
                Course Name: <span className="text-black">{student.courseTitle[0].courseTitle}</span>
              </p>
              <p className="fw-semibold text-primary">
                Duration: <span className="text-black">{student.courseTitle[0].courseDuration}</span>
              </p>
              <p className="fw-semibold text-primary mt-4 pb-3">
                Start Date: <span className="text-black">2024-05-12</span>
              </p>
            </>
          )}
        </div>

        <div className="col-12 shadow">
          <div className="table-responsive CourseAndPaymentCardTableHight mt-5">
            <table className="table">
              <thead>
                <tr>
                  <th scope="col">Subject ID</th>
                  <th scope="col">Subject Name</th>
                  <th scope="col">Class Day</th>
                  <th scope="col">Session Start Time</th>
                  <th scope="col">Session End Time</th>
                  <th scope="col">Join Class</th>
                </tr>
              </thead>
              <tbody>
                {subjects.length > 0 ? (
                  subjects.map((subject) => (
                    <tr key={subject._id}> {/* Assuming _id is the unique key */}
                      <td>{subject.subjectId}</td>
                      {/* Use subjectId instead of _id */}
                      <td>{subject.subjectName}</td>
                      <td>{subject.classDay}</td>
                      <td>{subject.sessionStartTime}</td>
                      <td>{subject.sessionEndTime}</td>
                      <td>   <div className="col-6 text-end">
                        <button className="btn btn-primary">Join</button>
                      </div></td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="5">No subjects found</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CourseDetailCard;
