import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";


const AddStudentEduction = () => {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
        fullName: "",
        certificateName: "",
        dob: "",
        gender: "",
        custodianName: "",
        whatsappMobileNo1: "",
        whatsappMobileNo2: "",
        schools: [""],
    });

    const regName = localStorage.getItem("studentName");


    useEffect(() => {
        const storedWhatsAppMobileNo1 = localStorage.getItem("whatsAppMobileNo1");

        if (storedWhatsAppMobileNo1) {
            setFormData((prevData) => ({
                ...prevData,
                whatsappMobileNo1: JSON.parse(storedWhatsAppMobileNo1), // Decode when retrieving
            }));
        } else {
            axios
                .get("https://lmsacademicserver.netlify.app/api/coordinatorAddStudent/getAllStudents")
                .then((response) => {
                    // Check the response structure
                    console.log("API Response:", response.data);

                    const loginStudent = response.data[0]; // Adjust based on your actual data structure
                    if (loginStudent) {
                        const mobileNumber = loginStudent.whatsappMobileNo1 || ""; // Ensure a default value
                        setFormData((prevData) => ({
                            ...prevData,
                            whatsappMobileNo1: mobileNumber,
                        }));
                    } else {
                        console.error("No logged-in student found.");
                    }
                })
                .catch((error) => {
                    console.error("Error fetching student details:", error);
                });
        }
    }, []);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [name]: value,
        }));
    };

    const handleSchoolChange = (index, value) => {
        const updatedSchools = [...formData.schools];
        updatedSchools[index] = value;
        setFormData((prev) => ({
            ...prev,
            schools: updatedSchools,
        }));
    };

    const handleAddSchool = () => {
        setFormData((prev) => ({
            ...prev,
            schools: [...prev.schools, ""], // Add new empty input field for school
        }));
    };

    const handleRemoveSchool = (index) => {
        const updatedSchools = formData.schools.filter((_, i) => i !== index);
        setFormData((prev) => ({
            ...prev,
            schools: updatedSchools,
        }));
    };

    const handleSubmit = async () => {
        // Check if any required field is missing
        const { fullName, certificateName, dob, gender, custodianName, whatsappMobileNo1, whatsappMobileNo2, schools } = formData;

        const token = localStorage.getItem("token");

        // If token is missing, redirect to sign-in
        if (!token) {
            alert("No token found. Please log in.");
            navigate("/login"); // Redirect to the login page
            return;
        }

        // if (!fullName || !certificateName || !dob || !gender || !custodianName || !whatsappMobileNo1 || !whatsappMobileNo2 || schools.length === 0) {
        //     alert("Please fill in all required fields.");
        //     return;
        // }

        const formattedData = {
            fullName,
            fullNameCertificate: certificateName, // Map to backend field
            dob,
            gender,
            custodiansName: custodianName, // Map to backend field
            whatsAppMobileNo1: localStorage.getItem("studentWP"),
            whatsAppMobileNo2: whatsappMobileNo2,
            school: schools, // Assuming the API expects an array here
        };

        console.log(formattedData);


        try {
            const response = await axios.post(
                "https://lmsacademicserver.netlify.app/api/studentDetails/addStudentDetails",
                formattedData,
                {
                    headers: {
                        Authorization: `Bearer ${token}`, // Add token in headers
                    },
                }
            );
            console.log("Response after submission:", response.data);

            alert("Student details added successfully");

            navigate("/profile"); // Navigate to profile on successful submission Done
        } catch (error) {
            if (error.response) {
                console.error("Error response from server:", error.response.data);
                alert(`Error: ${error.response.data.message || 'Failed to save data'}`);
            } else {
                console.error("Error saving data:", error.message);
                alert("Error saving data, please try again.");
            }
        }
    };



    return (
        <div className="form-bg-image">
            <div className="col-12 d-flex justify-content-center mt-4">
                <div className="row col-lg-6 shadow-lg m-3 rounded-3 h-auto mt-4 p-3 formColour border border-3 border-end-0 border-start-0 border-primary">
                    <h4 className="fw-bold text-black-50 mt-3 mb-4">Add Your Details</h4>


                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold">   
                        &nbsp;:</span>
                        <input
                            type="text"
                            className="form-control"
                            name="fullName"
                            placeholder="Full name"
                            value={regName}
                            // onChange={handleChange}
                            disabled
                        />
                    </div>

                    {/* Full Name */}
                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold"> &nbsp;:</span>
                        <input
                            type="text"
                            className="form-control"
                            name="fullName"
                            placeholder="Full name"
                            value={formData.fullName}
                            onChange={handleChange}
                        />
                    </div>

                    {/* Certificate Name */}
                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold">   &nbsp;:</span>
                        <input
                            type="text"
                            className="form-control"
                            name="certificateName"
                            placeholder="Name to be applied to certificate"
                            value={formData.certificateName}
                            onChange={handleChange}
                        />
                    </div>

                    {/* Date of Birth */}
                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold">&nbsp;&nbsp;:</span>
                        <input
                            type="date"
                            className="form-control"
                            name="dob"
                            value={formData.dob}
                            onChange={handleChange}
                        />
                    </div>

                    {/* Gender */}
                    <div className="col-12 mb-3">
                        <label className="input-group-text col-6 fw-bold"> &nbsp;&nbsp;:</label>
                        <div className="form-control">
                            <input
                                type="radio"
                                id="male"
                                name="gender"
                                value="Male"
                                className="form-check-input me-4 ms-2"
                                onChange={handleChange}
                                checked={formData.gender === "Male"}
                            />
                            <label className="form-check-label me-5" htmlFor="male">Male</label>

                            <input
                                type="radio"
                                id="female"
                                name="gender"
                                value="Female"
                                className="form-check-input ms-2 me-4 "
                                onChange={handleChange}
                                checked={formData.gender === "Female"}
                            />
                            <label className="form-check-label" htmlFor="female">Female</label>
                        </div>
                    </div>

                    {/* Custodian Name */}
                    {/* <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold"> &nbsp;&nbsp;:</span>
                        <input
                            type="text"
                            className="form-control"
                            name="custodianName"
                            placeholder="Custodian's Name"
                            value={formData.custodianName}
                            onChange={handleChange}
                        />
                    </div> */}

                    {/* Whatsapp Mobile No 1 */}
                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold">WhatsApp  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
                        {/* <input
                            type="text"
                            className="form-control"
                            name="whatsappMobileNo1"
                            placeholder="Enter Whatsapp Mobile No 1"
                            value={localStorage.getItem("studentWP")}
                            // onChange={handleChange}
                            readOnly
                        /> */}

                        <input
                            type="text"
                            className="form-control"
                            name="whatsappMobileNo1"
                            placeholder="Enter WhatsApp Mobile Number"
                            value={localStorage.getItem("studentWP")}
                            // onChange={handleChange}
                            disabled
                        />
                    </div>

                    {/* Whatsapp Mobile No 2 */}
                    <div className="col-12 mb-3">
                        <span className="input-group-text col-6 fw-bold">    
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</span>
                        <input
                            type="text"
                            className="form-control"
                            name="whatsappMobileNo2"
                            placeholder="Enter Another Contact Number"
                            value={formData.whatsappMobileNo2}
                            onChange={handleChange}
                        />
                    </div>

                    {/* Schools */}
                    {/* <div className="input-group mb-3">
                        <span className="input-group-text col-6 fw-bold">&nbsp;:</span>
                    </div>
                    {formData.schools.map((school, index) => (
                        <div key={index} className="d-flex mb-2">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="School"
                                value={school}
                                onChange={(e) => handleSchoolChange(index, e.target.value)}
                                required
                            />
                            {formData.schools.length > 1 && (
                                <button
                                    type="button"
                                    className="btn btn-danger ms-2"
                                    onClick={() => handleRemoveSchool(index)}
                                >
                                    Remove
                                </button>
                            )}
                        </div>
                    ))}
                    <button
                        type="button"
                        className="btn btn-primary"
                        onClick={handleAddSchool}
                    >
                          
                    </button> */}

                    {/* Submit Button */}
                    <div className="col-12 text-end">
                        <button type="button" className="btn btn-primary m-2"  style={{backgroundColor:"rgb(13, 13, 175)"}} onClick={handleSubmit}>
                            Save Personal Details
                        </button>
                        <button type="button" className="btn btn-dark m-2" onClick={() => navigate("/profile")}>
                            Back to profile
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AddStudentEduction;
